<template>
	<div id="mall-basic">
		<public-header title="基础设置"></public-header>
		<div class="content">
			<van-cell-group>
				<van-field v-model="mall.title" label="平台名称" placeholder="请输入平台名称" input-align="right" />
				<van-field v-model="mall.mobile" label="平台客服电话" placeholder="请输客服电话" input-align="right" />
				<van-field v-model="mall.delivery_title" label="配送方式名称" placeholder="请输入配送方式" input-align="right" />
			</van-cell-group>
			<van-radio-group class="margin-10-t" v-model="mall.store_orderby_type">
				<van-cell-group>
					<van-cell title="首页商家排序方式"></van-cell>
					<van-cell title="按排序大小排序" clickable @click="mall.store_orderby_type = 'displayorder'" class="border-0px">
						<van-radio name="displayorder" />
					</van-cell>
					<van-cell title="按热度" clickable @click="mall.store_orderby_type = 'click'" class="border-0px">
						<van-radio name="click" />
					</van-cell>
					<van-cell title="离我最近" clickable @click="mall.store_orderby_type = 'distance'" class="border-0px">
						<van-radio name="distance" />
					</van-cell>
					<van-cell title="销量最高" clickable @click="mall.store_orderby_type = 'sailed'" class="border-0px">
						<van-radio name="sailed" />
					</van-cell>
					<van-cell title="评分最高" clickable @click="mall.store_orderby_type = 'score'" class="border-0px">
						<van-radio name="score" />
					</van-cell>
					<van-cell title="先按照排序大小，再按照排序距离" clickable @click="mall.store_orderby_type = 'displayorderAndDistance'" class="border-0px sort-width">
						<van-radio name="displayorderAndDistance" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group  class="margin-10-t">
				<van-cell>
					<div slot="title">超出商家配送范围是否展示</div>
					<iswitch :value="mall.store_overradius_display" condition-open="1" condition-close="2" slot="right-icon" text-open="ON" text-close="OFF"  @change="jsToggleSwitch" keys="mall.store_overradius_display"></iswitch>
				</van-cell>
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				mall: {
					title: '',
					mobile: '',
					delivery_title: '',
					store_orderby_type: '',
					store_overradius_display: '1'
				}
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'mall',
					url: 'plateform/config/mall/basic'
				});
			},
			jsToggleSwitch(data) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},
			onSubmit() {
				var that = this;
				setData({
					vue: this,
					url: 'plateform/config/mall/basic',
					data: that.mall
				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	
</style>


